}

.nav ul li {
  display: inline-block;
  position: relative;
}

.nav ul li.logo {
  font-family: "ChiTown NF";
}

.nav ul li.logo h2 {
  font-size: 60px;
}

.nav ul li.logo:hover:before {
  display: none;
}

.nav ul li:before {
  display: block;
  content: '';
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 0;
